import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView'
import BoardView from '../views/SearchView'
import LoginView from '../views/LoginView'
import SignUpView from '../views/SignUpView'

import Vuebar from 'vuebar';



Vue.use(Vuebar);
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    alias: ['/index', '/main'],
    name: 'home',
    component: HomeView
  },
  {
    path: '/search',
    name: 'search',
    component: BoardView
  },
  {
    path: '/searchBar',
    name: 'searchBar',
    component: BoardView
  },
  {
    path: '/login',
    name: 'login',
    component : LoginView
  },
  {
    path: '/signup',
    name: 'signup',
    component : SignUpView
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
